  function getPageNumVal(a) {
    var text = a;
    a = a.split('/'); a = a[a.length-1];
    a = a.split('-')[0].split('_');
    
    var val = 0;
    for (var i = 0; i < a.length; i++) {
      var valSeg = parseFloat(a[i]);
      if (isNaN(valSeg)) {
        break;
      }
      
      val *= 100;
      val += valSeg;
    }
    
    val = (isNaN(val) || val == 0) ? -1 : val;
    //console.log('getPageNumVal("' + text + '") = ' + val);
    return val;    
  }
  
  angular
    .module('deckerApp')
    .config(routeConfig);

  var modulesPrefix = 'app/modules';

  function normalizeState(state) {
    
    var state = state.toLowerCase().replace('.html', '').replace(/\.|-|\s/g, '_');
    var stateComponents = state.split('/');
   
    var result = '/';
    for (var i in stateComponents) {
      if (stateComponents[i].length) {
        if (i < (stateComponents.length - 1)) {
          result += parseInt(stateComponents[i]) + '_';
        }
        else {
          result += stateComponents[i];
        }
      }
    }
    return result;
  }
  
  angular.module('deckerApp').normalizeState = normalizeState;

  window.updateAllRoutesRoute = function(routeFilter, stateProviderArg) {
    /*
    if (! $stateProvider) {
      $stateProvider = stateProviderArg;
    }*/
    stateProvider = stateProviderArg;
    window.lastRouteFilter = routeFilter;
    var templateCache = angular.module("deckerApp").templateCache;
    var routes = angular.module("deckerApp").routes = [];
    var modules = angular.module("deckerApp").modules = [];
    var isFirst = true;

    // get sorted list of the urls to pages in modules
    //var modulePageUrls = getModulePageUrls();
    var modulePageUrls = getModulePageUrlsForFilter(routeFilter);
    //console.log("modulePageUrls",modulePageUrls);
    var modulesToPagesMap = {};
    var modulesToPageCount = {};
    //var moduleMap = {};
    
    for (var iurl in modulePageUrls) {
      var url = modulePageUrls[iurl];
      if (url.indexOf('app/modules/0.') == 0) {
        continue;
      }

      var route = getRoute(url);
      if (!route) {
        continue;
      }
      
      var splitUrl = route.origStateUrl.split('/');
      var moduleName = splitUrl[1];
      
      var moduleNum = parseInt(moduleName) - 1;
      if (moduleNum >= modules.length) {
        var addCount = moduleNum - modules.length + 1;
        while (addCount--) {
          modules.push(null);
        }
      }
      
      if (! modules[moduleNum]) {
        modulesToPagesMap[moduleNum] = [];
        modulesToPageCount[moduleNum] = 0;
        var newModule = {
          name: moduleName
        };
        modules[moduleNum] = newModule;
      }
      
      if (url.indexOf('@') != -1) {
        var module = modules[moduleNum];
        var data = templateCache[url];
        try {
          data = JSON.parse(data.replace('<script>','').replace('</script>',''));
          module.name = data.title;
          module.length = data.length;
        }
        catch(e) {
          console.log('invalid data in ' + url);
        }
        continue;
      }

      var urlCapabilities = url.split('_')[2].split('-')[0];
      
      var inFlow = urlCapabilities.indexOf(routeFilter) != -1;
        
      if (inFlow) {
        modulesToPageCount[moduleNum] = 1 + modulesToPageCount[moduleNum];
        routes.push(route);
      }

      var stateUrl = '/' + splitUrl[2];
      
      if (stateProvider) {
        stateProvider
          .state(route.stateUrl.substr(0), {
            url: stateUrl, // route.stateUrl,
            templateUrl: url,
            controller: 'MainController',
            controllerAs: 'main',
            moduleNumber: moduleNum,
            pageNumber: modulesToPageCount[moduleNum] - 1,
            module: (moduleNum < modules.length) ? modules[moduleNum] : null
          });
      }

      if (inFlow && (! modules[moduleNum].firstPage) && inFlow) {
        modules[moduleNum].firstPage = stateUrl;
      }

      if (stateProvider && isFirst && inFlow && moduleName.indexOf('0') != 0) {
        route.stateUrl = '/';
        stateProvider
          .state('/', {
            url: '/',
            templateUrl: url,
            controller: 'MainController',
            controllerAs: 'main',
            moduleNumber: moduleNum,
            pageNumber: modulesToPageCount[moduleNum] - 1,
            module: (moduleNum < modules.length) ? modules[moduleNum] : null
          });
        isFirst = false;
      }
      
      if (moduleNum < modules.length) {
        modules[moduleNum].pageCount = modulesToPageCount[moduleNum];
      }
    }

    routes.sort(function(a,b) {
      a = getPageNumVal(a.url);
      b = getPageNumVal(b.url);
      return a - b;
    });
    /*stateProvider = null;*/
  }
  
