        $scope.addNewNote = function() {

          var state = $state.current.name;
          if (state.indexOf('/1_1_03') == 0) {
            state = '/1_1_03_can_setup';
          }

          var currentModule = $state.current.moduleNumber;
          if (!currentModule) {
            currentModule = 0;
          } else {
            currentModule = Math.max(0, Math.min(6, currentModule));
          }

          var newNote = globals.persistent.progress.modules[currentModule].newNote;
          if(newNote && newNote != '')
          {
            globals.persistent.progress.modules[currentModule].newNote = '';
            if (currentModule != globals.notepadModule) {
              var moduleNotes = globals.persistent.moduleNotes[globals.notepadModule];
              var lastNotes;
              for (var key in moduleNotes) {
                lastNotes = moduleNotes[key];
              }
              if (lastNotes) {
                lastNotes.push(newNote);
              }
              return;
            }

            var notesForModule = globals.persistent.moduleNotes[currentModule];
            if (!notesForModule.hasOwnProperty(state)) {
              notesForModule[state] = [];
            }
            if (!newNote) {
              newNote = "";
            }
            // if (state == '/') {
            //   state == '/1_1_01_can_intro';
            // }
            notesForModule[state].push(newNote);
          }
          
        }

