        $scope.isPageCompleted = function(module, page) {
          if (!globals.persistent.progress.modules || !globals.persistent.progress.modules[module - 1] || !globals.persistent.progress.modules[module - 1][page - 1]) {
            return false;
          }

          return globals.persistent.progress.modules[module - 1][page - 1].completion;
        }

        $scope.goToModule = function(module) {
          var curModule = $state.current.moduleNumber;
          if (curModule != module) {
            var page = (module == 0) ? '1_1' : globals.modules[module].firstPage;
            if (page && !globals.persistent.progress.modules[module].locked) {
              $scope.go(page);
            }
          }
        }

