        $scope.$root.getLastRecording = function(vrecorderId) {
          if (globals.persistent && globals.persistent.vrecorders && globals.persistent.vrecorders[vrecorderId]) {
            var recordings = globals.persistent.vrecorders[vrecorderId].recordings;
            if (!recordings) {
              recordings = globals.persistent.vrecorders[vrecorderId] = [];
            }
            if (recordings.length) {
              return recordings[recordings.length - 1];
            }
          }
          return null;
        }

        $scope.$root.clearRecordings = function(vrecorderId) {
          globals.persistent = globals.persistent || {};
          globals.persistent.vrecorders = globals.persistent.vrecorders || {};
          globals.persistent.historyvrecorders = globals.persistent.historyvrecorders || {};
          if(vrecorderId == 'page7_03')
          {
            var audienceChoice =  0;
            if(globals.persistent.audienceChoice || globals.persistent.audienceChoice >= 0)
            {
              audienceChoice = globals.persistent.audienceChoice;
            }
            globals.persistent.historyvrecorders[vrecorderId] = globals.persistent.historyvrecorders[vrecorderId] || {};
            globals.persistent.historyvrecorders[vrecorderId][audienceChoice] = globals.persistent.vrecorders[vrecorderId] || {
              recordings: []
            };
            globals.persistent.historylabFlagging = globals.persistent.historylabFlagging || {};
            globals.persistent.historylabFlagging[audienceChoice] = globals.persistent.labFlagging || {};
            globals.persistent.labFlagging = {};

          }
          else
          {
            globals.persistent.historyvrecorders[vrecorderId] = globals.persistent.vrecorders[vrecorderId] || {
              recordings: []
            };  
          }          
          globals.persistent.vrecorders[vrecorderId] = {
            recordings: []
          };
          $scope.$root.getRecordingStatus();
          globals.redoRecording = false;
          $scope.setPageComplete(0);

          $timeout(function() {
              jQuery(document).find('a.workstationopen').click(function(e) {
              e.preventDefault();
              $rootScope.$broadcast('recordopenWorkstation');
            });
          }, 250);
        }

