        function adjustSplitter() {
          function onMouseUpAfterSplitterDrag() {
            if (isDraggingSplitter) {
              isDraggingSplitter = false;
              var newWorkspaceWidth = $('.workspace').width();

              if (Math.abs(newWorkspaceWidth - startDraggingWorkspaceWidth) < 5) {
                // just a click, so toggle
                if (newWorkspaceWidth < 50) {
                  $('.main-left-side').css('right', '49%');
                  $('.splitter-divider').css('right', '49%');
                  $('.workspace').css('width', '49%');
                } else {
                  $('.main-left-side').css('right', '0%');
                  $('.splitter-divider').css('right', '0%');
                  $('.workspace').css('width', '0');
                }
              } else {
                if (newWorkspaceWidth < 100) {
                  $('.main-left-side').css('right', '0%');
                  $('.splitter-divider').css('right', '0%');
                  $('.workspace').css('width', '0');
                } else if (newWorkspaceWidth < 300) {
                  $('.main-left-side').css('right', '49%');
                  $('.splitter-divider').css('right', '49%');
                  $('.workspace').css('width', '49%');
                }
              }
            }
          }

          if (!splitterDivider || splitterDivider.length == 0) {
            splitterDivider = $('.splitter-divider');
            if (splitterDivider.length) {

              splitterDivider.on('mousedown touchdown', function() {
                if (!isDraggingSplitter) {
                  isDraggingSplitter = true;
                  startDraggingWorkspaceWidth = $('.workspace').width();
                  $(window).on('mouseup touchup', onMouseUpAfterSplitterDrag);
                }
              });

            }
          }
        }

