  window.onRecorderCommandMsg = function(e) {
    try
    {
      //console.log("onRecorderCommandMsg");
      //console.log(document.VideoRecorder);
      if (!document.VideoRecorder) {
        return;
      }
      
      if (document.VideoRecorder[e]) {
        switch (e) {
          case 'record':
            
            document.VideoRecorder.record();
            break;
          case 'pauseRecording':
            document.VideoRecorder.pauseRecording();
            break;
          case 'resumeRecording':
            document.VideoRecorder.resumeRecording();
            break;
          case 'stopVideo':
            document.VideoRecorder.stopVideo();
            break;
          case 'playVideo':
            document.VideoRecorder.playVideo();
            break;
          case 'pause':
            document.VideoRecorder.pause();
            break;
          case 'save':
            document.VideoRecorder. save();
            break;
          case 'startRecording':
            document.VideoRecorder.startRecording();
            break;
        }
      }
    } catch (err) {
        console.log('onRecorderCommandMsg threw exception :' , err);
    }
  };

