  window.onUploadDone = function(streamName, streamDuration, userId, recorderId, audioCodec, videoCodec, fileType) {
    sendMessage( {
      evt : 'onUploadDone',
      streamName : streamName,
      streamDuration : streamDuration,
      userId : userId,
      recorderId : recorderId,
      audioCodec : audioCodec,
      videoCodec : videoCodec,
      fileType : fileType
    });

    // this function is called when the video/audio stream has been all sent to
    // the video server and has been saved to the video server HHD,
    // on slow client->server connections, because the data can not reach the
    // video server in real time, it is stored in the recorder's buffer until it
    // is sent to the server, you can configure the buffer size in
    // avc_settings.XXX

    // this function is called with 7 parameters:
    // streamName: a string representing the name of the stream recorded on the
    // video server WITHOUT the filename extension (.flv , .f4v or .mp4)
    // userId: the userId sent via flash vars or via the avc_settings.XXX file,
    // the value in the avc_settings.XXX file takes precedence if its not empty
    // duration of the recorded video/audio file in seconds but acccurate to the
    // millisecond (like this: 4.322)
    // recorderId: the recorderId sent via flash vars, to be used when there are
    // many recorders on the same web page
    // audioCodec: the audio codec used for the recording, Nelly Moser or Speex
    // videoCodec: the video codec used for the recording, Sorenson or H.264
    // fileType: the format in which the resulting recording will be saved in:
    // FLV, F4V or MP4
  }

