              var length = wavData.length;
              var bar = width / duration; //Math.round(width/duration);//Math.round((width-duration)/duration) - 1; //Math.round((width/10) * 1);                    
              var perbar = Math.floor(wavData.length / duration);
              var remaining = wavData.length - (perbar * duration);
              var after = duration - remaining;
              var gap = 1; //((width - (bar*duration))/duration);// Math.max(1, ~~(bar / 2)); 
              $ = ((width - (bar * duration)) / duration);
              var step = Math.round(bar + gap);
              absmax = Math.max.apply(null, wavData);
