/**
 * @ngdoc directive
 * @name deckerApp.directive:videoFlagging
 * @description
 * # videoFlagging
 */
angular.module('deckerApp')
  .directive('videoFlagging', function () {
    return {
      templateUrl: 'app/scripts/directives/videoflagging.html',
      restrict: 'E',
      scope: {
        flags: '=',
        data: '=',
        playerid: '@',
        recorderid: '@'
      },
      link: function postLink(scope, element, attrs) {

        function pauseVideo() {
          scope.$root.pauseVideo(scope.playerid);
          scope.isPausedToFlag = true;
        }

        function playVideo() {
          scope.$root.playVideo(scope.playerid);
          scope.isPausedToFlag = false;
        }

        scope.flags = scope.flags || [];

        scope.getFlagPos = function (flag) {

          if (flag.time === undefined) {
            return 0;
          }
          else {
            return scope.$root.getFlagPosition(flag.time);
          }
          
          var flagVideoPlayback = angular.element($("#flagVideoPlayback")).scope();
          
          var recorder = scope.$root.getLastRecording(scope.recorderid);

          if (scope.durationSeconds) {
            return $("#flaggingTimeline").width() *
              flag.time / 1000 / scope.durationSeconds;
          }
          return 0;
        }

        scope.removeFlag = function (index) {
          if (scope.flags.length == 1) {
            scope.flags = [];
          } else {
            scope.flags.splice(index, 1);
          }
        }

        scope.onClickToFlag = function () {
          pauseVideo();          
        }

        scope.onAddFlag = function () {
                    
          var flagText = $("#flagText").val();
          $("#flagText").val("");
          
          var flag = {
            text: flagText,
            time: scope.$root.videos[scope.playerid].pos
          };
          scope.flags.push(flag);
          scope.flags.sort(function (a, b) {
            return a.time > b.time;
          });

          playVideo();
        }
      }
    }
  });
