/**
 * @ngdoc directive
 * @name deckerApp.directive:ShowSource
 * @description
 * # ShowSource
 */
angular.module('deckerApp')
  .directive('showSource', function () {
    return {
      templateUrl: 'app/scripts/directives/showsource.html',
      transclude: true,
      restrict: 'E', 
      
      link: function postLink(scope, element, attrs) {

        var annotation = {};
        var annotationElem = element.find("#annotation");
        var sections = annotationElem.children("section");
        var details = annotationElem.children("details");
        for (var i = 0; i < sections.length; i++) {
          annotation[sections.eq(i).text()] = details.eq(i).text();
        }
        annotationElem.detach();
        
        var liveCode = element.find(".live-code");        
        var srcHtml;

        srcHtml = liveCode.html().trim();

        var startComment = srcHtml.indexOf("<!--");
        if (startComment != -1) {
          srcHtml = srcHtml.substr(startComment + 4, srcHtml.length - startComment - 7);
          html = srcHtml.replace(/</g, "&lt;").replace(/>/g, "&gt;");
        }
        else {

        var tagCount = 0;
        var insideTag = false;
        var inClosingTag = false;
        var skipWhitespace = true;
        var inQuote = false;
        var inSingleQuote = false;
        var nestedTags = 0;
        
        var html = '';
        for (var i = 0; i < srcHtml.length; i++) {
          var ch = srcHtml[i];
          var nextCh = (i + 1) < srcHtml.length ? srcHtml[i + 1] : '';
          
          if ((inQuote || inSingleQuote) && (ch == '<' || ch == '>')) {
            html += ch;
            continue;
          }
          
          
          switch (ch) {
              
            case '<':
              if (nextCh == '/') {
                html = html.trim();
                inClosingTag = true;
                --tagCount;
              }

              if (html.length && !srcHtml.substr(i).startsWith('</p>')) {
                html = html.trim();
                html += '<br>';
                for (var j = 0; j < tagCount; j++) {
                  html += '&nbsp;&nbsp;';
                }
              }

              html += '&lt;';
              if (! inClosingTag) {
                ++tagCount;
              }
              break;
              
            case '>':
              if (inClosingTag) {
                //--tagCount;
                inClosingTag = false;
                
              }
              html += '&gt;';
              skipWhitespace = true;
              
              //if (html.endsWith('&lt;br&gt;')) {
              //  --tagCount;
              //}
              break;
              
            case ' ':
            case '\r':
            case '\n':
              if (html.endsWith('&lt;input')) {
                --tagCount;
              }
                  
              if (! skipWhitespace) {
                html += ' ';
              }
              break;

            case 13:
              break;

            default:
              if (ch == "'") { inSingleQuote = ! inSingleQuote; } 
              if (ch == '"') { inQuote = ! inQuote; } 
              
              html += ch;
              skipWhitespace = false;
              
          }
        }
        }
        for (var key in annotation) {
          var val = annotation[key];
          val = val.replace(/'/g, "&apos");
          var replaceWith = "<span class='annotated' title='" + val + "'>" + key + "</span>";
          html = html.replace(key, replaceWith);
        }

        element.find(".show-source").html(html);
      }
    };
  });
