angular.module('deckerApp')
  .directive('hslider', function () {
    return {
      templateUrl: 'app/scripts/directives/hslider.html',
      restrict: 'E',
      scope: {
        value: '=',
        min: '=',
        max: '=',
        onchange: '&'
      },
      link: function postLink(scope, element, attrs) {
        
        var dragging = false;
        
        var slider = element.find('.hslider');
        var thumb = element.find('.hslider-thumb');

        function adjustThumb() {          
          
          var fraction = (scope.value - scope.min) / (scope.max - scope.min);
          
          var numPos = scope.max - scope.min + 1;
          var sliderW = slider.width();
          var thumbW = sliderW / numPos;
          thumb.css('left', (fraction * (sliderW-thumbW)) + 'px');
        }
        
        scope.$watchCollection('[value, min, max]', 
                              function() { if (! dragging) adjustThumb(); });
        
        scope.onMouseDown = function(event) {
          event.preventDefault();
          event.stopPropagation();
          
          var sliderRect = slider[0].getBoundingClientRect();
          var left = sliderRect.left + 20;
          var right = left + sliderRect.width - 40;
          
          dragging = true;
          var page = slider.parents('.page-holder');
          var body = $(document.body);
          body.mousemove(function(event) {
            if (dragging ) {
              //event.preventDefault();
              //event.stopPropagation();

              var x = event.clientX;
              var val = scope.min + (scope.max - scope.min) * (x - left) / (right - left);
              val = Math.max(scope.min, Math.min(scope.max, val));

              if (val >= scope.min && val <= scope.max) {
                scope.value = val;
                adjustThumb();
                if (scope.onchange) {
                  scope.onchange();
                }
              }
            }
          });
          $(window).mouseup(function(event) {
            if (dragging) {
              event.preventDefault();
              event.stopPropagation();
              dragging = false;
              scope.value = parseInt(scope.value + .5);
              adjustThumb();
              scope.$apply();
              if (scope.onchange) {
                scope.onchange();
              }
            }
          });
        }
      }
    }
  });
