        var moduleroutes = angular.module("deckerApp").moduleroutes;
        var module = modules[moduleNumber];//$state.current.module;
        var moduleroute = moduleroutes[moduleNumber];
        for (var i = 0; i < module.pageCount; i++) {
          if (! progress.modules[moduleNumber].hasOwnProperty(i) ||
              typeof progress.modules[moduleNumber][i] != 'object') {
            //console.log('creating progress for ' + moduleNumber + ', ' + i);
            progress.modules[moduleNumber][i] = {completion: 0, current: 0, maxViewed: 0};
          }
        }
        var pageProgress = progress.modules[moduleNumber][pageNumber];
        if (! pageProgress) {
          return;
        }
        if (completion == 1 && isNaN(videoPos)) {
          videoPos = videoDuration = pageProgress.videoDuration;
          console.log(videoDuration);
        }
