(function() {

  'use strict';
  
    queryString = function() {
    // This function is anonymous, is executed immediately and
    // the return value is assigned to queryString!
    var query_string = {};
    var query = document.location.search.substring(1);
    var vars = query.split("&");
    for ( var i = 0; i < vars.length; i++) {
      var pair = vars[i].split("=");
      // If first entry with this name
      if (typeof query_string[pair[0]] === "undefined") {
        query_string[pair[0]] = decodeURIComponent(pair[1]);
        // If second entry with this name
      } else if (typeof query_string[pair[0]] === "string") {
        var arr = [ query_string[pair[0]], decodeURIComponent(pair[1]) ];
        query_string[pair[0]] = arr;
        // If third or later entry with this name
      } else {
        query_string[pair[0]].push(decodeURIComponent(pair[1]));
      }
    }
    return query_string;
  }();


  function reverse(s){
    return s.split("").reverse().join("");
  }
  
  // dev mode only!!!
  
  
  var authenticated = false;
  var root;
  var queryString;
  var _progressService;
  var _globals = null;
  var _$timeout;
  var hadOpener = false;
  
  function redirect() {
    
    // in case we weren't authenticated, redirect to decker.com
    if (_globals.ALLOW_LOCAL_VIEWING) {
      return;
    }
    
    var redirectUrl = "//decker.com";
    window.location.href = redirectUrl;
  }
  
  /**
   * @ngdoc service
   * @name deckerApp.Authentication
   * @description # Authentication Service in the deckerApp.
   */
  angular.module('deckerApp').service('scormService',
      [ '$rootScope', '$timeout', '$interval', 'progressService', 'globals',
        function($rootScope, $timeout, $interval, progressService, globals) {        
        
        globals.SCORM_DISABLED =   
          (window.location.href.indexOf('staging') != -1) ||
          (window.location.href.indexOf('final') != -1) ||
          (window.location.href.indexOf('cti_app') != -1) ||
          (window.location.href.indexOf('ctiapp') != -1) ||
          (window.location.href.indexOf('stable') != -1) ||
          (window.location.href.indexOf('alpha') != -1) ||
          (window.location.href.indexOf('local') != -1) ||
          (window.location.href.indexOf('test') != -1) ||
          (window.location.href.indexOf('sales') != -1) ||
          (window.location.href.indexOf('demo') != -1) ||
          (window.location.href.indexOf('localhost') != -1)|| 
          (window.location.href.indexOf('10.0.2.2') != -1); 

        //globals.SCORM_DISABLED = false; - testing
        
        globals.DEV_MODE =
          globals.SCORM_DISABLED ||
          window.location.pathname == '/sales/' ||
          window.location.pathname == '/staging/' ||
          window.location.pathname == '/final/' ||
          window.location.pathname == '/demo/' ||
          window.location.pathname == '/local/' ||
          window.location.pathname == '/cti_app/' ||
          window.location.pathname == '/ctiapp/' ||
          window.location.pathname == '/stable/' ||
          window.location.pathname == '/alpha/';

        if (! globals.ALLOW_LOCAL_VIEWING) {
          globals.ALLOW_LOCAL_VIEWING = globals.DEV_MODE;        
        }
        
        root = $rootScope;
        _$timeout = $timeout;
        _progressService = progressService;
        _globals = globals;
        
        this.save = function(data) {
          window.localStorage.setItem('persistent', JSON.stringify(data));
        }
        
        // if we had the opener, then watch for it to be closed. if so, redirect.
        $interval(function() {
          if (window.opener) {
            hadOpener = true;
          }
          else if (hadOpener) {
            redirect();
          }          
        },250);
        
        
        if (globals.DEV_MODE) {
          // in dev mode, don't wait to show the body
          $('body').css('display', 'block');
        } else {
          if (! window.parent && ! window.opener) {
            redirect();
            return;
          }
          
          if (queryString.c1 && queryString.c2) {
            var c1 = reverse(queryString.c1);
            var c2 = '0.' + reverse(queryString.c2);
            // if the date time is over ten seconds different, redirect
            var dateTime = parseFloat(c1) * parseFloat(c2);
            var timeDiff = Math.abs(dateTime - new Date().getTime()); 
            if (timeDiff > 30000) {
              // if we don't have the url parameters, redirect
              redirect();
            }
          }
          else {
            // if we don't have the url parameters, redirect
            redirect();
          }

          window.setTimeout(function() {
            if (! authenticated) {
              redirect();
            }
          }, 5000)
        }        
      } ]);


  window.sendScormMessage = function(type, data) {
    //console.log("call sendScormMessage");
    if (_globals.SCORM_DISABLED) {
      return;
    }
    
    //console.log('sendScormMessage, type=' + type + ', data = ');
    //console.log(data);
    
    if (window.parent) {
      if (!data) {
        data = {};
      }

      data.scormMsg = type;
      if (window.opener) {
        window.opener.postMessage(data, '*');
      }
      else {
        window.parent.postMessage(data, '*');
      }
    }
  }

  $(window).on('message', function(je) {

    var e = je.originalEvent;
    if (e && e.data && e.data.scormMsg) {
      //console.log('app received msg: ' + e.data.scormMsg);
      switch (e.data.scormMsg) {
      case 'authenticate':
        if (!authenticated) {
          if (e.data.c1 == queryString.c1 && e.data.c2 == queryString.c2) {
            authenticated = true;
            _globals.SCORM_DISABLED = false;
            this.authenticated = true;
            var suspend_data = e.data.suspend_data ? e.data.suspend_data : '{}';

            //console.log('suspend_data = ');
            //console.log(suspend_data);

            _progressService.setScormData(suspend_data);
            _progressService.resumeLastPage();

            $('body').css('display', 'block');
            sendScormMessage('ready');
            _$timeout(function(){}, 0);
          }
        }
      }
    }
  });
  
//Fires an event on the window that opened it
  window.onbeforeunload = function () {
    sendScormMessage('closed');
  };

})();
